@font-face {
  font-family: "opensans";
  font-style: normal;
  font-weight: 400; /*regular*/
  src: local("opensans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "opensans";
  font-style: normal;
  font-weight: 300;
  src: local("opensanslight"), url("../fonts/opensanslight.woff2") format("woff2"),
    url("../fonts/opensanslight.woff") format("woff");
  font-display: swap;
}

@font-face {
  font-family: "opensans";
  font-style: normal;
  font-weight: 900;
  src: local("opensansbold"), url("../fonts/opensansbold.woff2") format("woff2"),
    url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
