.header-menu {
}

.header-menu__toggle {
  display: inline-block;
  position: absolute;
  top: 25px;
  right: 20px;
  cursor: pointer;
  padding: 0;
  outline: none;
  border: none;
  width: 23px;
  height: 23px;
  background: url("../img/icon-menu-cross.svg") center no-repeat;

  &--no-js {
    display: none;
  }

  &--cross {
    width: 50px;
    background: url("../img/icon-menu-burger.svg") center no-repeat;
  }
  @media (min-width: $width-tablet+px) {
    top: 43px;
  }

  @media (min-width: $width-desktop+px) {
    display: none;
    font-size: 0;
    border: none;
  }
}

.header-menu__wrapper {
  width: 100%;

  @media (max-width: $width-desktop+px) {
    display: none;

    &--shown {
      display: block;
    }

    &--no-js {
      display: block;
    }
  }
}
