.header {
  font-family: "opensans", "Arial", sans-serif;
  background: transparent;
  height: auto;
  position: relative;
  z-index: 3;
  background-color: rgba($color: #000000, $alpha: 0.3);
  &--gray {
    background: #283645;
  }
}
.header__body {
  padding-top: 23px;
  min-height: 66px;
  @media (min-width: $width-tablet+px) {
    min-height: 110px;
  }
  @media (min-width: $width-desktop+px) {
    padding: 0;
    display: flex;
    align-items: center;
  }
}
.header__logo {
  display: inline-block;
  height: 23px;
  margin-bottom: 21px;
  svg {
    fill: $color_white;
  }
  @media (min-width: $width-tablet+px) {
    width: 145px;
    height: 40px;
    margin-bottom: 43px;
    svg {
      width: 145px;
      height: 40px;
    }
  }
  @media (min-width: $width-desktop+px) {
    margin: 0;
    width: 147px;
    position: relative;
    top: -10px;

    &:hover {
      svg {
        fill: $color_pink;
      }
    }
    &:active {
      svg {
        fill: rgba($color: #fff, $alpha: 0.7);
      }
    }
  }
}
.logo-mobile {
  @media (min-width: $width-tablet+px) {
    display: none;
  }
}
.logo-tablet {
  display: none;
  @media (min-width: $width-tablet+px) {
    width: 145px;
    height: 40px;
    display: inline-block;
  }
  @media (min-width: $width-desktop+px) {
    display: none;
  }
}
.logo-desktop {
  display: none;
  @media (min-width: $width-desktop+px) {
    width: 147px;
    height: 40px;
    display: inline-block;
  }
}
.header-menu {
  margin: 0 -20px;

  @media (min-width: $width-desktop+px) {
    margin: 0;
    display: flex;
    width: 100%;
    flex: 1 1 auto;
  }
}
.header-menu__toggle {
}
.header-menu__toggle--no-js {
}
.visually-hidden {
}
.header-menu__wrapper {
}
.header-menu__wrapper--no-js {
}
.header-menu__list {
}
.nav-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  @media (min-width: $width-desktop+px) {
    display: flex;
  }
}
.nav-menu__item {
  width: 100%;
  text-align: center;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  @media (min-width: $width-desktop+px) {
    box-shadow: none;
  }
}
.nav-menu__link {
  display: inline-block;
  font-weight: bold;
  font-size: 18px;
  line-height: 13px;
  height: 13px;
  color: $color_white;
  text-decoration: none;
  text-transform: uppercase;
  padding: 25px 0;
  @media (min-width: $width-desktop+px) {
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
    height: 18px;
    padding: 19px 0 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
    position: relative;
    width: max-content;
    margin: 0 auto;
    &:hover {
      color: $color_pink;
    }
    &:active {
      color: rgba($color: #fff, $alpha: 0.7);
    }
    &--active {
      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: $color_white;
      }
    }
  }
}
