.footer {
  font-family: "opensans", "Arial", sans-serif;
  background: $color_concrete;
}

.footer__container {
  box-sizing: border-box;
  display: grid;
  height: 150px;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);

  @media (max-width: $width-tablet+px) {
    height: 144px;
  }

  @media (max-width: 600px) {
    height: auto;
    grid-template-columns: 1fr;
    grid-template-rows: minmax(95px, auto) 63px 78px;
  }
}

.footer__logo {
  box-sizing: border-box;
  display: inline-block;
  width: 128px;
  height: 24px;

  @media (max-width: 600px) {
    margin: 37px auto 32px;
  }

  @media (min-width: $width-mobile+px) {
    &:hover {
      fill: rgba($color: #000000, $alpha: 0.8);
    }

    &:active {
      fill: rgba($color: #000000, $alpha: 0.6);
    }
  }
}

.footer__social {
  box-sizing: border-box;
  height: 100%;
  font-family: "opensans", "Arial", sans-serif;
  padding: 0;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  min-width: 167px;
}

.footer__social-item {
  list-style: none;
}

.footer__social-link {
  fill: $color_pink;
  display: flex;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 3px solid #e9ebec;
  align-items: center;
  justify-content: center;

  @media (min-width: $width-mobile+px) {
    &:hover {
      fill: #68b738;
    }

    &:active {
      fill: rgba($color: #68b738, $alpha: 0.6);
    }
  }
}

.footer__social-link--twitter {
  svg {
    width: 18px;
    height: 14px;
  }
}

.footer__social-link--facebook {
  svg {
    width: 9px;
    height: 18px;
  }
}

.footer__social-link--youtube {
  svg {
    width: 15px;
    height: 13px;
  }
}

.footer__copy {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: nowrap;

  @media (max-width: 600px) {
    justify-content: center;
  }
}

.footer__text {
  font-family: "Arial", sans-serif;
  margin: 0;
  margin-right: 24px;
  font-size: 16px;
  line-height: 18px;
}

.footer__academy-logo {
  color: $color_tundora;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: nowrap;
  fill: $color_pink;
  text-decoration: none;

  @media (min-width: $width-mobile+px) {
    &:hover {
      fill: #68b738;
    }
  }

  &:active {
    color: #b3b3b3;
    fill: rgba($color: #68b738, $alpha: 0.6);
  }

  @media (max-width: 600px) {
    width: 100%;
    justify-content: space-between;
  }
}
.logo-pink-blue-desktop {
  &--letter {
    @media (max-width: $width-mobile+px) {
      display: none;
    }
  }
}
