.entry {
  background-color: #1d2631;
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover 462px;
  &--menu-open {
    background-position: center 252px;
  }
  @media (min-width: $width-tablet+px) {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-position: bottom center;
  }
  @media (min-width: $width-desktop+px) {
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }
}
.entry__promo {
  font-family: "opensans", "Arial", sans-serif;
}
.promo {
}
.promo__container {
  position: relative;
  z-index: 2;
  margin: 0;
  padding: 25px 20px 38px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media (min-width: $width-tablet+px) {
    padding: 102px 20px 103px;
    align-items: flex-start;
  }
  @media (min-width: $width-desktop+px) {
    max-width: $max-width;
    margin: 0 auto;
  }
}
.promo__title {
  margin: 0 0 26px;
  padding: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  color: $color_white;
  display: inline-block;
  width: 280px;
  text-align: center;
  br {
    display: none;
  }
  @media (min-width: $width-tablet+px) {
    font-size: 40px;
    line-height: 48px;
    margin-bottom: 39px;
    width: 300px;
    min-height: 127px;
    br {
      display: block;
    }
  }
  @media (min-width: $width-desktop+px) {
    font-size: 50px;
    line-height: 68px;
    min-height: 174px;
  }
}
.promo__img-hand {
  margin: 0 0 28px -60px;
  width: 260px;
  box-sizing: border-box;
  z-index: -1;
  @media (min-width: $width-tablet+px) {
    width: 660px;
    height: 545px;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0;
  }
  @media (min-width: 1200px) {
    width: 1050px;
    height: 645px;
    right: -130px;
  }
}
.promo__download-link {
  display: inline-block;
  width: 280px;
  height: 55px;
  border: 3px solid #ffffff;
  box-sizing: border-box;
  border-radius: 29px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $color_white;
  text-decoration: none;
  font-weight: 900;
  font-size: 18px;
  line-height: 18px;
  margin-bottom: 32px;
  @media (min-width: $width-mobile+px) {
    &:hover {
      color: $color_pink;
      background-color: $color_white;
    }
  }
  @media (min-width: $width-tablet+px) {
    margin-bottom: 46px;
    width: 300px;
  }
}
.promo__os-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 28px;
  @media (min-width: $width-tablet+px) {
    margin-bottom: 38px;
    width: 205px;
    margin-left: 46px;
  }
}
.promo__os-item {
}
.promo__text {
  margin: 0;
  text-align: center;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: $color_white;
  @media (min-width: $width-tablet+px) {
    width: 300px;
  }
}
