.visually-hidden {
  visibility: hidden;
  display: none;
}

.btn {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: $color_white;
  text-transform: uppercase;
  font-size: 16px;

  @media (min-width: $width-mobile+px) {
    &:hover {
      background: #5eaa2f;
    }
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
