.effect {
  padding: 110px 0 109px;
  @media (max-width: $width-tablet+px) {
    display: none;
  }
  @media (min-width: $width-desktop+px) {
    background: #f2f2f2;
    padding: 17px 0 67px;
  }
}
.effect__container {
}
.container {
}
.effect__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  grid-template-rows: auto minmax(82px, auto) min-content;
}
.effect__timer {
  box-sizing: border-box;
  font-weight: bold;
  font-size: 96px;
  line-height: 96px;
  color: $color_pink;
  display: inline-block;
  width: 202px;
  height: 202px;
  background-color: transparent;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 16px solid $color_pink;
  position: relative;
  z-index: 2;
  margin: 56px 0 20px 20px;
  grid-column: 1/2;
  grid-row: 1/4;
  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    bottom: -34px;
    left: -34px;
    width: 238px;
    height: 277px;
    background: url("../img/icon-timer.svg") center center no-repeat;
    background-size: contain;
  }
  &::before {
    box-sizing: border-box;
    content: "";
    position: absolute;
    z-index: 1;
    width: 202px;
    height: 202px;
    border-radius: 50%;
    border: 16px solid $color_pink;
    top: -16px;
    left: -16px;
  }
}
.effect__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  margin: 0;
  align-self: end;
  @media (min-width: $width-desktop+px) {
    margin-bottom: 30px;
    br {
      display: none;
    }
  }
}
.effect__text {
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  margin: 0;
}
.effect__link {
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $color_pink;
}
