.feedback {
  &__container {
    min-height: 364px;
    padding-top: 50px;
    padding-bottom: 57px;
    @media (min-width: $width-tablet+px) {
      min-height: 405px;
    }
    @media (min-width: $width-desktop+px) {
      padding-top: 70px;
    }
  }

  &__slider-wrapper {
    position: relative;
    min-height: 257px;
    max-height: 100%;
  }
  &__slider-list {
    margin: 0;
    padding: 0;
    min-height: 100%;
    list-style-type: none;
    position: relative;
  }

  &__slider-dots-list {
    width: 100%;
    height: auto;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 3;
    margin: 0;
    padding: 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (min-width: $width-desktop+px) {
      display: none;
    }
  }

  span.sim-dot {
    width: 6px;
    height: 6px;
    margin: 5px 7px;
    padding: 0;
    display: inline-block;
    background-color: #ffffff;
    border: 3px solid black;
    border-radius: 50%;
    cursor: pointer;
  }

  &__slider-btn {
    display: none;
    @media (min-width: $width-desktop+px) {
      display: block;
      border: none;
      outline: none;
      background-color: transparent;
      margin: 0;
      padding: 0;
      font-size: 0;
      width: 22px;
      height: 41px;
      position: absolute;
      cursor: pointer;
      opacity: 0.6;
      z-index: 4;
      fill: #bfc3c7;
      &:focus {
        outline: 2px solid black;
        outline-offset: 2px;
      }
    }
  }

  &__slider-btn--prev {
    left: 10px;
    top: 40%;
  }

  &__slider-btn--next {
    right: 10px;
    top: 40%;
  }
}
.feedback-item {
  font-family: "opensans", "Arial", sans-serif;
  width: 100%;
  height: auto;
  transition: opacity 1s ease-in;
  opacity: 0;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  display: block;
  @media (min-width: $width-tablet+px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  @media (min-width: $width-desktop+px) {
    padding-top: 121px;
  }
}

.feedback-item__author {
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: $color_black;
  text-align: center;
  margin: 0 0 31px;
  @media (min-width: $width-tablet+px) {
    font-size: 20px;
    margin-bottom: 10px;
  }
  @media (min-width: $width-desktop+px) {
    font-size: 24px;
  }
}

.feedback-item__author-info {
  display: none;
  margin: 0;
  @media (min-width: $width-tablet+px) {
    display: block;
    font-size: 14px;
    line-height: 30px;
    text-align: center;
    color: #283645;
  }
  @media (min-width: $width-desktop+px) {
    font-size: 18px;
  }
}

.feedback-item__text {
  margin: 0;
  font-family: "opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: #283645;
  @media (min-width: $width-tablet+px) {
    font-size: 18px;
    order: -1;
    margin-bottom: 45px;
  }
  @media (min-width: $width-desktop+px) {
    width: 688px;
    position: relative;
    &::after {
      content: "";
      width: 79px;
      height: 63px;
      display: block;
      background: url("../img/icon-quotes.svg") center no-repeat;
      background-size: contain;
      position: absolute;
      top: -121px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
