$max-width: 960px;
$width-desktop: 939.98;
$width-tablet: 659.98;
$width-mobile: 320;

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  @media (min-width: $width-desktop+px) {
    max-width: $max-width;
  }
}

.container-fluid {
  min-width: 320px;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
