.features {
  font-family: "opensans", "Arial", sans-serif;
  box-sizing: border-box;
  background-image: linear-gradient(351deg, rgba(242, 242, 242, 1) 76%, rgba(255, 255, 255, 1) 76%);
  @media (min-width: $width-tablet+px) {
    background-image: linear-gradient(355deg, rgba(242, 242, 242, 1) 61%, rgba(255, 255, 255, 1) 61%);
  }
  @media (min-width: $width-desktop+px) {
    background-image: linear-gradient(355deg, rgba(242, 242, 242, 1) 59%, rgba(255, 255, 255, 1) 59%);
  }
}
.features__container {
  padding-top: 46px;
  @media (min-width: $width-tablet+px) {
    padding-top: 58px;
  }
  @media (min-width: $width-desktop+px) {
    padding-top: 74px;
  }
}
.features__title {
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  color: #283645;
  text-align: center;
  margin: 0 0 35px;
  &--last {
    margin-bottom: 123px;
  }
  @media (min-width: $width-tablet+px) {
    font-size: 40px;
    line-height: 54px;
    margin-bottom: 0;
    &--last {
      margin-bottom: 45px;
    }
  }
  @media (min-width: $width-desktop+px) {
    font-size: 50px;
    line-height: 68px;
    &--last {
      margin-bottom: 70px;
    }
  }
}
.features__body {
  display: grid;
  justify-items: center;
  overflow: hidden;
  position: relative;
  @media (min-width: $width-tablet+px) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: minmax(200px, auto) minmax(145px, auto) minmax(140px, auto);
    align-items: center;
    min-height: 482px;
  }
  @media (min-width: $width-desktop+px) {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto max-content;
    align-items: stretch;
  }
}
.features__img--desktop {
  display: none;
  @media (min-width: $width-desktop+px) {
    display: inline-block;
  }
}
.features__img--all-viewport {
  grid-row: 5;
  position: relative;
  bottom: -43px;
  @media (min-width: $width-tablet+px) {
    grid-row: 1/4;
    grid-column: 1/2;
    bottom: -3px;
    align-self: flex-start;
    justify-self: flex-start;
    img {
      width: 268px;
      height: 481px;
    }
  }
  @media (min-width: $width-desktop+px) {
    grid-row: auto;
    grid-column: auto;
    bottom: 0;
    justify-self: center;
    img {
      width: 292px;
      height: 302px;
    }
  }
}
.feautures__item {
  &--first {
    align-self: end;
  }
  &--last {
    align-self: start;
  }
  @media (min-width: $width-desktop+px) {
    padding: 59px 30px;
  }
}
.feautures__item-title {
  font-family: "opensans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 14px;
  color: $color_black;
  margin: 0 0 24px;
  text-align: center;
  @media (min-width: $width-tablet+px) {
    text-align: left;
    margin-bottom: 17px;
  }
  @media (min-width: $width-desktop+px) {
    text-align: center;
    font-size: 30px;
    line-height: 30px;
    font-weight: 900;
  }
}
.feautures__item-text {
  font-family: "opensans", "Arial", sans-serif;
  margin: 0 0 43px;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  text-align: center;
  &--m0 {
    margin-bottom: 0;
  }
  @media (min-width: $width-tablet+px) {
    text-align: left;
    margin: 0 0 30px;
  }
  @media (min-width: $width-desktop+px) {
    text-align: center;
    font-size: 18px;
    line-height: 30px;
    margin: 0;
  }
}
